import * as React from "react";
import { Container } from "@/components/layout/container";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "@/lib/utils";

const gridVariants = cva(
  "grid grid-cols-2 divide-x divide-y [&>*:first-child]:border-t [&>*:first-child]:border-l [&>*:last-child]:!border-r",

  {
    variants: {
      variant: {
        2: "",
        6: `
          sm:grid-cols-3 md:grid-cols-6

          [&>*:nth-child(6n+2)]:!border-r [&>*:nth-child(6n+4)]:!border-r
          sm:[&>*:nth-child(6n+2)]:!border-r-0 sm:[&>*:nth-child(6n+4)]:!border-r-0

          sm:[&>*:nth-child(6n+3)]:!border-r
          md:[&>*:nth-child(6n+3)]:!border-r-0
        `,
        8: `
          sm:grid-cols-4 lg:grid-cols-8

          [&>*:nth-child(8n+2)]:!border-r [&>*:nth-child(8n+4)]:!border-r [&>*:nth-child(8n+6)]:!border-r
          sm:[&>*:nth-child(8n+2)]:!border-r-0 sm:[&>*:nth-child(8n+4)]:!border-r-0 sm:[&>*:nth-child(8n+6)]:!border-r-0

          sm:[&>*:nth-child(8n+4)]:!border-r
          lg:[&>*:nth-child(8n+4)]:!border-r-0
        `,
      },
    },
    defaultVariants: {
      variant: 2,
    },
  },
);

interface GridProps extends VariantProps<typeof gridVariants> {
  cells: Array<{
    href?: string;
    render: () => JSX.Element;
  }>;
}

const Grid = ({ variant, cells }: GridProps) => {
  return (
    <Container>
      <div className={cn(gridVariants({ variant }))}>
        {cells.map((cell, index) => {
          const magicCardContent = (
            <div
              key={index}
              className="group relative flex size-full items-center justify-center overflow-hidden transition-colors hover:bg-secondary/20"
            >
              {cell.render()}
            </div>
          );

          return cell.href ? (
            <a
              key={index}
              href={cell.href}
              target="_blank"
              rel="noopener noreferrer"
            >
              {magicCardContent}
            </a>
          ) : (
            magicCardContent
          );
        })}
      </div>
    </Container>
  );
};

export { Grid };
