import * as React from "react";
import { Container } from "@/components/layout/container";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import Ripple from "@/components/magicui/ripple";
import { ArrowUpRight } from "lucide-react";
import { toast } from "sonner";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { z } from "zod";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope } from "@fortawesome/free-solid-svg-icons";
import { faLinkedin } from "@fortawesome/free-brands-svg-icons";

const contactPoints = [
  {
    label: "E-mail",
    icon: faEnvelope,
    value: "hello@brytonlacquement.com",
    href: "mailto:hello@brytonlacquement.com",
  },
  {
    label: "LinkedIn",
    icon: faLinkedin,
    value: "@bryton-lacquement",
    href: "https://www.linkedin.com/in/bryton-lacquement/",
  },
];

const formSchema = z.object({
  fullname: z.string().min(2).max(50),
  email: z.string().email(),
  message: z.string().min(2).max(1000),
});

const ContactSection = () => {
  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      fullname: "",
      email: "",
      message: "",
    },
  });

  const [isSubmitting, setIsSubmitting] = React.useState(false);

  function onSubmit(values: z.infer<typeof formSchema>) {
    setIsSubmitting(true);

    const body = new FormData();
    body.append("fullname", values.fullname);
    body.append("email", values.email);
    body.append("message", values.message);
    body.append("access_key", "ed540b59-53c0-4868-ab5c-14789c5a9ffb");

    fetch("https://api.web3forms.com/submit", { method: "POST", body })
      .then((response) => response.json())
      .then((data) => {
        if (!data.success) {
          throw new Error(data);
        }
        form.reset();
        toast.success("Merci ! Votre message a bien été envoyé.");
      })
      .catch((error) => {
        toast.error(
          "Erreur lors de l'envoi du message. Merci de réessayer plus tard.",
        );
        console.log("Error", error);
      })
      .finally(() => setIsSubmitting(false));
  }

  return (
    <Container>
      <div className="relative overflow-hidden border-x border-t py-16 text-center">
        <p className="mx-auto max-w-3xl text-balance text-3xl font-medium text-foreground">
          Vous souhaitez me contacter ?
        </p>
        <p className="mx-auto mt-6 max-w-prose text-balance font-medium text-muted-foreground">
          Utilisez le formulaire ci-dessous ou contactez-moi directement avec
          mes coordonnées.
        </p>
        <Ripple />
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3">
        <div className="col-span-1 border-x border-t lg:border-r-0">
          {contactPoints.map((contactPoint, index) => (
            <div
              key={index}
              className="border-b p-4 transition-colors last:mb-4 hover:bg-secondary/20"
            >
              <h3 className="font-medium tracking-tight">
                <FontAwesomeIcon
                  className="me-2"
                  icon={contactPoint.icon}
                  fixedWidth
                />
                {contactPoint.label}
              </h3>
              <p className="text-sm text-muted-foreground">
                <a
                  className="group hover:text-foreground hover:underline"
                  href={contactPoint.href}
                >
                  {contactPoint.value}
                  <ArrowUpRight className="ms-2 inline-block size-4 opacity-0 transition-opacity group-hover:opacity-100" />
                </a>
              </p>
            </div>
          ))}
        </div>

        <div className="col-span-2 border-x border-t">
          <Form {...form}>
            <form
              onSubmit={form.handleSubmit(onSubmit)}
              className="flex flex-col gap-4 p-4"
            >
              <FormField
                control={form.control}
                name="fullname"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Nom complet</FormLabel>
                    <FormControl>
                      <Input
                        className="rounded-none"
                        placeholder="Votre nom complet"
                        disabled={isSubmitting}
                        {...field}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="email"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>E-mail</FormLabel>
                    <FormControl>
                      <Input
                        className="rounded-none"
                        placeholder="Votre adresse e-mail"
                        disabled={isSubmitting}
                        {...field}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="message"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Message</FormLabel>
                    <FormControl>
                      <Textarea
                        className="rounded-none"
                        placeholder="Votre message"
                        rows={7}
                        disabled={isSubmitting}
                        {...field}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <Button
                className="mt-2 rounded-none"
                variant={form.formState.isValid ? "default" : "secondary"}
                size="lg"
                type="submit"
                disabled={isSubmitting}
              >
                {isSubmitting ? "Envoi en cours…" : "Envoyer le message"}
              </Button>
            </form>
          </Form>
        </div>
      </div>
    </Container>
  );
};

export { ContactSection };
